(function() {
  'use strict';

  angular
    .module('teambookWww')
    .directive('courseList', courseList);


  /** @ngInject */
  function courseList() {
      var directive = {
          restrict: 'E',
          templateUrl: 'app/college/courseList/course_list.html',
          scope: {},
          controller: CourseListController,
          controllerAs: 'vm',
          bindToController: true,
          replace: true
      };

      return directive;

      /** @ngInject */
      function CourseListController($location,courses,$scope) {
          var vm = this;
          var userId = $location.search().userId;
          vm.courses = [];
          vm.courses = getCourse();

          $scope.$on('changeCat',function(e,data){
              vm.catFilter = data;
          });

          function getCourse(){
              var results = courses.getCourses();

              angular.forEach(results,function(c,index){
                  var tags = [];
                  tags = c.courseTags.split(',');
                  c.courseTagsArr = tags;

              });

              return results;
          }
      }
  }

})();

(function() {
  'use strict';

  angular
    .module('teambookWww')
    .directive('courseCat', courseCat);


  /** @ngInject */
  function courseCat() {
      var directive = {
          restrict: 'E',
          templateUrl: 'app/college/courseCat/course_cat.html',
          scope: {},
          controller: CourseCatController,
          controllerAs: 'vm',
          bindToController: true,
          replace: true
      };

      return directive;

      /** @ngInject */
      function CourseCatController($location,$scope) {
          var vm = this;
          var userId = $location.search().userId;

          vm.showCourse = showCourse;

          /*切换课程分类
          * @param cat:分类id
          * */
          function showCourse(cat) {
              //console.log($scope.$parent.catFilter);
              switch (cat) {
                  case "cat1":
                      $scope.$emit('catClick','行政');
                      break;
                  case "cat2":
                      $scope.$emit('catClick','技术');

                      break;
                  case "cat3":
                      $scope.$emit('catClick','财务');

                      break;
                  case "cat4":
                      $scope.$emit('catClick','市场');
                      break;
              }
          }
      }
  }

})();

(function () {
  'use strict';

  angular
    .module('teambookWww')
    .factory('user', User);

  /** @ngInject */
  function User($cookies,$location,  $http, $q, teambookConfig) {

    var COOKIE_USER_NAME = "user";

    var service = {
      login: login,
      logout: logout,
      getUser: getUser,
      checkAuth: checkAuth
    };

    return service;

    /**
     * 提供登陆操作
     * @param cb 回调函数
     * @param username
     * @param password
     */
    function login(username, password,autoLogin) {

        if(autoLogin){
            var date = new Date();
            date.setDate(date.getDate() + 7);
            var expires = date;
        }
      var login_url = teambookConfig.apiHost + "/api/user/login";

      return $http.post(login_url, {username: username, password: password})
        .then(loginComplete)
        .catch(loginFailed);

      function loginComplete(res) {

            //console.log("res :" +res);
            var user = res.data;
            $cookies.putObject(COOKIE_USER_NAME, user,{"expires":expires});
            return user;
      }

      function loginFailed(error) {
        return $q.reject(error);
      }


    }

    /**
     * 用户登出
     * @param cb
     */
    function logout() {
      $cookies.remove(COOKIE_USER_NAME);
    }

    /**
     * 获取授权的用户
     * @param cb
     */
    function getUser() {
      return $cookies.getObject(COOKIE_USER_NAME);
    }

    /**
     * 检查是否登陆
     * @param cb
     */
    function checkAuth() {
        if(!getUser()){
            $location.path('/login');
        }
    }
  }
})();

